@keyframes textScale {
  0% {
    transform: scale(1, 1);
    opacity: 1;
    transform-origin: bottom left;
  }
  25% {
    transform: scale(0.5, 0.8);
    opacity: 0.4;
    transform-origin: bottom left;
  }
  50% {
    transform: scale(0, 0.5);
    opacity: 0;
    transform-origin: bottom left;
  }
  75% {
    transform: scale(0.8, 0.8);
    opacity: 0.4;
    transform-origin: top right;
  }
  100% {
    transform: scale(1, 1);
    opacity: 1;
    transform-origin: top right;
  }
}
@keyframes buttonScale {
  0% {
    transform: scale(1, 1);
    opacity: 1;
    transform-origin: left;
  }
  25% {
    transform: scale(0.8, 0.6);
    opacity: 0.4;
    transform-origin: left;
  }
  50% {
    transform: scale(0, 0.8);
    opacity: 0;
    transform-origin: right;
  }
  75% {
    transform: scale(0.8, 0.6);
    opacity: 0.4;
    transform-origin: right;
  }
  100% {
    transform: scale(1, 1);
    opacity: 1;
    transform-origin: right;
  }
}
@keyframes sidebarOpen {
  from {
    transform: scaleX(0);
  }
  to {
    transform: scaleX(1);
  }
}
@keyframes sidebarClose {
  from {
    transform: scaleX(1);
  }
  to {
    transform: scaleX(0);
  }
}
@keyframes fadeMenu {
  from {
    background: rgba(8, 8, 8, 0.7);
  }
  to {
    background: transparent;
  }
}
@keyframes menuButton {
  0% {
    background-size: 0 0;
    background-image: radial-gradient(circle, rgba(255, 255, 255, 0.4) 30%, rgba(254, 254, 254, 0.3) 70%);
  }
  33% {
    background-size: 3.8rem 3.8rem;
    background-image: radial-gradient(circle, rgba(255, 255, 255, 0.5) 30%, rgba(254, 254, 254, 0.4) 70%);
  }
  67% {
    background-size: 3.8rem 3.8rem;
    background-image: radial-gradient(circle, rgba(255, 255, 255, 0.5) 30%, rgba(254, 254, 254, 0.4) 70%);
  }
  100% {
    background-size: 3.8rem 3.8rem;
    background-image: radial-gradient(circle, transparent, transparent);
  }
}
#text-animate {
  animation: textScale 0.5s ease;
}
#button-animate {
  animation: buttonScale 0.5s ease;
}
#menu {
  animation-duration: 0.4s;
